import React from 'react';

import {
  IndexPageQuotesText,
  IndexPageQuotesAuthorText,
} from '../components/Typography/Typography';
import Button from '../components/UI/Button/Button';
import CircularAvatar from '../components/UI/CircularAvatar/CircularAvatar';
import Head from '../components/Head/Head';

import classes from './index.module.scss';
import swamiji from '../assets/images/swami-vivekananda.jpg';

const home = () => {
  return (
    <div className={classes.IndexPageBody}>
      <Head content="Satadru Roy's home page">home | Satadru Roy</Head>
      <div className={classes.IndexPageHeading}>
        <IndexPageQuotesText>
          Do one thing at a time, and while doing it put your whole soul into it
          to the exclusion of all else.
        </IndexPageQuotesText>
      </div>
      <div className={classes.AuthorContainer}>
        <IndexPageQuotesAuthorText>
          &#8212; Swami Vivekananda
        </IndexPageQuotesAuthorText>
        <CircularAvatar className={classes.CircularAvatar}>
          <img
            src={swamiji}
            alt="Swami Vivekananda"
            className={classes.Swamiji}
          />
        </CircularAvatar>
      </div>
      <div className={classes.AboutUsBtnOnIndexPage}>
        <Button to="/about/">About Me</Button>
      </div>
    </div>
  );
};

export default home;
